.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-float infinite 3s ease-in-out;
            animation: App-logo-float infinite 3s ease-in-out;
  }
}



@font-face {
	font-family: Carrois;
	src: url(/static/media/CarroisGothicSC-Regular.abf54f9a.ttf);
  }

.App-header {
